@use 'src/index' as general;

.shoppingCart {
  @include general.generalFont;
  grid-column: 1/4;
  grid-row: 1/4;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  // z-index: 0;
  position: relative;
  overflow: auto;
  right: 0;
  background: linear-gradient(
    to right,
    rgba(10, 10, 10, 0.564) 50%,
    transparent 50%
  );

  background-size: 200% 100%;
  background-position: right bottom;
  transition: all 0.5s ease-out;
  &.visible {
    visibility: visible;
    background-position: left bottom;
  }
  &.notVisible {
    visibility: hidden;
  }
}

.cartContainer {
  width: 25vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  padding: 20px;
  height: 100%;
  transition: all ease-out;
  max-height: 100vh;
  button {
    width: 50%;
  }
}

.products {
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: 160px;
  gap: 20px;
  max-height: 60vh;
  overflow: auto;
  flex-grow: 1;
  margin-top: 50px;
}
.cartControls {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  margin-bottom: 20px;
}
.reset,
.checkout,
.closeCart {
  @include general.generalFont;
  border: none;
  color: white;
  background-color: black;
  height: 3.2rem;
  font-size: 1.3rem;
  font-weight: 550;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  &:hover {
    cursor: pointer;
    transform: translateY(-3px);
    box-shadow: 4px 5px 2px rgb(0, 0, 0);
    color: black;
    background-color: white;
  }
  &:active {
    transform: translateY(-1px);
    box-shadow: 3px 4px 2px rgb(0, 0, 0);
  }
}

.totalPrice {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  height: 4rem;
  margin-top: 40px;
  margin-bottom: 20px;
}

.reset {
  margin-top: 20px;
}

.closeCart {
  position: absolute;
  right: 10px;
  top: 10px;
  width: calc(25vw + 20px);
  color: black;
  background-color: white;
}
